import Link from "next/link";
import { Phone, Mail, MapPin, Clock, ArrowUpRight } from "lucide-react";

const quickLinks = [
  { name: "Ana Səhifə", href: "/" },
  { name: "Haqqımızda", href: "/about" },
  { name: "Məhsullar", href: "/products" },
  { name: "Xidmətlər", href: "/services" },
  { name: "Sertifikatlar", href: "/certificates" },
  { name: "Əlaqə", href: "/contact" },
];

const services = [
  "Lift Satışı",
  "Layihələndirmə",
  "Quraşdırma",
  "Modernləşdirmə",
  "Texniki Xidmət",
  "Təcili Təmir",
];

export default function Footer() {
  return (
    <footer className="bg-[#0f172a] text-white">
      {/* Main Footer */}
      <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 py-16">
        <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-4 gap-12">
          {/* Company Info */}
          <div className="space-y-6">
            <Link href="/" className="inline-block mb-6">
              <div className="relative h-28 w-80 bg-white/5 rounded-lg p-2">
                 <img
                  src="/images/logo.png"
                  alt="KasperLift"
                  className="object-contain w-full h-full"
                />
              </div>
            </Link>
            <p className="text-gray-400 text-sm leading-relaxed">
              13+ illik təcrübə ilə Azərbaycanın aparıcı lift və eskalator şirkəti.
              Keyfiyyətli məhsullar və professional xidmət.
            </p>
            <div className="flex space-x-4">
              <a href="#" className="w-10 h-10 bg-white/10 rounded-full flex items-center justify-center hover:bg-blue-600 transition-colors">
                <svg className="w-5 h-5" fill="currentColor" viewBox="0 0 24 24">
                  <path d="M24 12.073c0-6.627-5.373-12-12-12s-12 5.373-12 12c0 5.99 4.388 10.954 10.125 11.854v-8.385H7.078v-3.47h3.047V9.43c0-3.007 1.792-4.669 4.533-4.669 1.312 0 2.686.235 2.686.235v2.953H15.83c-1.491 0-1.956.925-1.956 1.874v2.25h3.328l-.532 3.47h-2.796v8.385C19.612 23.027 24 18.062 24 12.073z"/>
                </svg>
              </a>
              <a href="#" className="w-10 h-10 bg-white/10 rounded-full flex items-center justify-center hover:bg-blue-600 transition-colors">
                <svg className="w-5 h-5" fill="currentColor" viewBox="0 0 24 24">
                  <path d="M12 2.163c3.204 0 3.584.012 4.85.07 3.252.148 4.771 1.691 4.919 4.919.058 1.265.069 1.645.069 4.849 0 3.205-.012 3.584-.069 4.849-.149 3.225-1.664 4.771-4.919 4.919-1.266.058-1.644.07-4.85.07-3.204 0-3.584-.012-4.849-.07-3.26-.149-4.771-1.699-4.919-4.92-.058-1.265-.07-1.644-.07-4.849 0-3.204.013-3.583.07-4.849.149-3.227 1.664-4.771 4.919-4.919 1.266-.057 1.645-.069 4.849-.069zm0-2.163c-3.259 0-3.667.014-4.947.072-4.358.2-6.78 2.618-6.98 6.98-.059 1.281-.073 1.689-.073 4.948 0 3.259.014 3.668.072 4.948.2 4.358 2.618 6.78 6.98 6.98 1.281.058 1.689.072 4.948.072 3.259 0 3.668-.014 4.948-.072 4.354-.2 6.782-2.618 6.979-6.98.059-1.28.073-1.689.073-4.948 0-3.259-.014-3.667-.072-4.947-.196-4.354-2.617-6.78-6.979-6.98-1.281-.059-1.69-.073-4.949-.073zm0 5.838c-3.403 0-6.162 2.759-6.162 6.162s2.759 6.163 6.162 6.163 6.162-2.759 6.162-6.163c0-3.403-2.759-6.162-6.162-6.162zm0 10.162c-2.209 0-4-1.79-4-4 0-2.209 1.791-4 4-4s4 1.791 4 4c0 2.21-1.791 4-4 4zm6.406-11.845c-.796 0-1.441.645-1.441 1.44s.645 1.44 1.441 1.44c.795 0 1.439-.645 1.439-1.44s-.644-1.44-1.439-1.44z"/>
                </svg>
              </a>
              <a href="#" className="w-10 h-10 bg-white/10 rounded-full flex items-center justify-center hover:bg-blue-600 transition-colors">
                <svg className="w-5 h-5" fill="currentColor" viewBox="0 0 24 24">
                  <path d="M19.59 6.69a4.83 4.83 0 01-3.77-4.25V2h-3.45v6.4a2.89 2.89 0 01-5.42 0V2H4.05v14.84c0 1.52.13 3.03.38 4.5h3.52c.14-.66.25-1.33.31-2h.06a5.2 5.2 0 009.54-.01h.06c.05.67.16 1.34.3 2h3.52c.25-1.47.38-2.98.38-4.5V2h-5.02v6.4a2.89 2.89 0 01-2.89 2.87 2.86 2.86 0 01-1.42-.38v4.13a5.2 5.2 0 009.54-.01h.06c.05.67.16 1.34.3 2h3.52c.25-1.47.38-2.98.38-4.5V2h-5.02v6.4a2.89 2.89 0 01-2.89 2.87 2.86 2.86 0 01-1.42-.38v4.13a5.2 5.2 0 009.54-.01h.06c.05.67.16 1.34.3 2h3.52c.25-1.47.38-2.98.38-4.5V2h-5.02v6.4a2.89 2.89 0 01-2.89 2.87z"/>
                </svg>
              </a>
            </div>
          </div>

          {/* Quick Links */}
          <div>
            <h3 className="text-lg font-semibold mb-6">Sürətli Keçidlər</h3>
            <ul className="space-y-3">
              {quickLinks.map((link) => (
                <li key={link.name}>
                  <Link
                    href={link.href}
                    className="text-gray-400 hover:text-white transition-colors flex items-center group"
                  >
                    <ArrowUpRight className="w-4 h-4 mr-2 opacity-0 group-hover:opacity-100 transition-opacity" />
                    {link.name}
                  </Link>
                </li>
              ))}
            </ul>
          </div>

          {/* Services */}
          <div>
            <h3 className="text-lg font-semibold mb-6">Xidmətlərimiz</h3>
            <ul className="space-y-3">
              {services.map((service) => (
                <li key={service}>
                  <span className="text-gray-400">{service}</span>
                </li>
              ))}
            </ul>
          </div>

          {/* Contact Info */}
          <div>
            <h3 className="text-lg font-semibold mb-6">Əlaqə</h3>
            <div className="space-y-4">
              <div className="flex items-start space-x-3">
                <MapPin className="w-5 h-5 text-blue-400 mt-1 flex-shrink-0" />
                <span className="text-gray-400 text-sm">
                  STN Business Center, 10Q Ələsgər Qayıbov küç, AZ 1029, Bakı, Azərbaycan
                </span>
              </div>
              <div className="flex items-center space-x-3">
                <Phone className="w-5 h-5 text-blue-400 flex-shrink-0" />
                <div className="text-sm">
                  <div className="text-gray-300">Satış: +994 50 209 64 40</div>
                  <div className="text-gray-300">Texniki: +994 77 272 50 50</div>
                </div>
              </div>
              <div className="flex items-center space-x-3">
                <Mail className="w-5 h-5 text-blue-400 flex-shrink-0" />
                <a href="mailto:[email protected]" className="text-gray-300 text-sm hover:text-white transition-colors">
                  [email protected]
                </a>
              </div>
              <div className="flex items-center space-x-3">
                <Clock className="w-5 h-5 text-blue-400 flex-shrink-0" />
                <span className="text-gray-300 text-sm">
                  B.e - Cümə: 09:00 - 18:00
                </span>
              </div>
            </div>
          </div>
        </div>
      </div>

      {/* Bottom bar */}
      <div className="border-t border-white/10">
        <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 py-6">
          <div className="flex flex-col md:flex-row justify-between items-center space-y-4 md:space-y-0">
            <p className="text-gray-400 text-sm">
              © {new Date().getFullYear()} KasperLift MMC. Bütün hüquqlar qorunur.
            </p>
            <div className="flex items-center space-x-6 text-sm text-gray-400">
              <Link href="#" className="hover:text-white transition-colors">Məxfilik Siyasəti</Link>
              <Link href="#" className="hover:text-white transition-colors">İstifadə Şərtləri</Link>
            </div>
          </div>
        </div>
      </div>
    </footer>
  );
}
